<template>
    <div class="search" @keyup.enter="search">
        <form-create v-model="formObj" :rule="rule" :option="options" @btn-search="search" @btn-reset="reset">
        </form-create>
        <div class="li">
            <el-button type="primary" @click="add">新 增</el-button>
            <!-- <el-button type="default" @click="down">导 出</el-button> -->
        </div>
    </div>
</template>

<script>
    import SearchInputs from "../components/SearchInputs.vue";
    export default {
        extends: SearchInputs,
        props: {

        },
        data() {
            return {
                //表单生成规则
                options: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '70px'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                rule: [{
                    type: 'input',
                    field: "userName",
                    title: '手机号：',
                    value: '',
                    props: {
                        placeholder: '请输入手机号',
                    },
                    col: {
                        span: 7,
                    }
                }, {
                    type: 'input',
                    field: "realName",
                    title: '姓名：',
                    value: '',
                    props: {
                        placeholder: '请输入姓名',
                    },
                    col: {
                        span: 7,
                    }
                }, {
                    type: 'SubmitBtns',
                    field: 'btn',
                    emit: ['search', 'reset'],
                    col: {
                        span: 2,
                        offset: 8,
                    }
                }]
            }
        },
        mounted() {
            this.search();
        },
        methods: {
            search() {
                const formData = this.formObj.formData();
                const opt = {
                    ...formData,
                }
                for (let key in opt) {
                    if (opt[key] == '' || opt[key] == undefined) delete opt[key];
                }
                this.$emit('searchHand', opt)
            },
            add() {
                this.$emit('add')
            }
        }
    }
</script>
<style lang="scss">
    .search {
        padding: 24px 0 0px 0;

        .el-form-item {
            margin-bottom: 0.16rem;
        }

        .li {
            display: flex;
            margin-bottom: 16px;
        }
    }
</style>