<!--index 父类-->
<script>
    import TableCommon from "@/components/TableCommon.vue";
    export default {
        extends: TableCommon, //继承通用TableCommon
        name: 'index-parent',
        data() {
            return {
                doType: '编辑',
                formObj: {},
                options: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '90px'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                rule: [],
                currEditRow: '', //当前更改的数据
            }
        },
        methods: {
            handleClose() {
                this.dialogShow = false;
            },
            //根据字段名称找到规则
            _findRule(type) {
                return this.rule.find(r => r.field === type);
            },
            //公共提示 和刷新表格
            notice() {
                this.dialogShow = false;
                this.loading = false; //加载
                this.$notify({
                    title: '成功',
                    message: '操作成功',
                    type: 'success',
                    duration: 3000,
                });
                this.reloadTable();
            },
        },
    }
</script>