<!--井队用户管理-->
<template>
    <BoxTitle>
        <SearchInputs @searchHand="searchHand" @add="addHand"></SearchInputs>
        <Table :tableData="tableData" @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"
            :pageNum="page.pageNum" @btnClick="btnClick" :pageSize="page.pageSize" :total="total">
        </Table>
        <!--弹窗编辑或新增-->
        <el-dialog title="新增井队用户" custom-class="cusDialog search" :visible.sync="this.addDialogShow" width="5rem"
            :before-close="handleCloseAdd">
            <form-create v-model="addFormObj" :rule="addRule" :option="options">
            </form-create>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" :loading="loading" @click="addUser">确 定</el-button>
                <el-button @click="handleCloseAdd">关 闭</el-button>
            </span>
        </el-dialog>
        <!--弹窗绑定-->
        <el-dialog :title="'绑定计量点 ('+currEditRow.realName+'/'+currEditRow.cellphone + ')'" custom-class="cusDialog"
            :visible.sync="this.dialogShow" width="6rem" :before-close="handleClose" :destroy-on-close="true">
            <div class="content-title">已绑定（蓝色代表当前激活）：</div>
            <div class="noData" v-show="!points.length">暂未绑定</div>
            <el-tag :key="item.code" v-for="item in points" closable :type="item.alive==1?'':'success'"
                :disable-transitions="false" @close="deletePoint(item)">
                {{item.jpName}}
            </el-tag>
            <div class="line"></div>
            <div class="content-title marginTop add-content">
                <div v-show="showAdd" class="input-div">
                    <form-create v-model="formObj" :rule="rule" :option="options">
                    </form-create>
                </div>
                <div v-show="showAdd" class="add-btns">
                    <el-button type="primary" @click="add">确 定</el-button>
                    <el-button type="default" @click="cancel">取消</el-button>
                </div>
                <el-button v-show="!showAdd" type="primary" @click="addClick">新增</el-button>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="handleClose">关 闭</el-button>
            </span>
        </el-dialog>
        <!--弹窗绑定用电记录-->
        <el-dialog :title="'绑定用电记录 ('+currEditRow.realName+'/'+currEditRow.cellphone + ')'" custom-class="cusDialog"
            :visible.sync="this.dialogShowJpRecord" width="8rem" :before-close="handleCloseJpRecord"
            :destroy-on-close="true">
            <div class="content-title">已绑定记录：</div>
            <div class="item">
                <el-button v-show="selectedRows.length" type="danger" icon="el-icon-delete" @click="removeRecord">解 绑
                </el-button>
                <span v-show="selectedRows.length" class="notice">已选择 {{selectedRows.length}}
                    项记录</span>
            </div>
            <el-table row-key="index" max-height="300" :data="bindRecordTableData"
                :header-cell-style="{background:'#E6EAFE',color:'#606266'}" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column label="序号" type="index" width="70" />
                <el-table-column label="用能单位" prop="wkOrg" show-overflow-tooltip> </el-table-column>
                <el-table-column label="计量点名称" prop="jpName" show-overflow-tooltip></el-table-column>
                <el-table-column label="用电性质" prop="wkTy" width="100"></el-table-column>
            </el-table>
            <div class="page pagination-box">
                <el-pagination layout="total" :total="recordtotal">
                </el-pagination>
            </div>
            <div v-if="!showAddContent">
                <el-button type="primary" @click="showAddContent = true">新 增</el-button>
                </el-button>
            </div>
            <div v-if="showAddContent">
                <div class="item">
                    <form-create v-model="recordForm" :rule="addRecordRule" :option="recordOptions"
                        @btn-search="recordSearch" @btn-reset="recordReset">
                    </form-create>
                </div>
                <div class="item">
                    <el-table ref="searchTable" row-key="index" max-height="300" :data="searchRecordTableData"
                        :header-cell-style="{background:'#E6EAFE',color:'#606266'}"
                        @selection-change="handleSelectionChangeSearchRecord">
                        <el-table-column type="selection" width="55"></el-table-column>
                        <el-table-column label="用能单位" prop="wkOrg" show-overflow-tooltip> </el-table-column>
                        <!-- <el-table-column label="平台名称" prop="kpName" width="120"></el-table-column> -->
                        <el-table-column label="计量点名称" prop="jpName" show-overflow-tooltip></el-table-column>
                        <el-table-column label="用电性质" prop="wkTy" width="100"></el-table-column>
                    </el-table>
                </div>
                <el-button type="primary" @click="addRecord" :disabled="selectedRowsSearchRecord.length==0">提 交
                </el-button>
                </el-button>
                <el-button type="default" @click="cancelAddRecord">取 消</el-button>
                </el-button>
                <span v-show="selectedRowsSearchRecord.length" class="notice">已选择 {{selectedRowsSearchRecord.length}}
                    项记录</span>
            </div>

            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogShowJpRecord = false">关 闭</el-button>
            </span>
        </el-dialog>
    </BoxTitle>
</template>
<script>
    //组件
    import BoxTitle from "@/components/BoxTitle.vue";
    import Index from "../components/Index.vue";
    import Table from "./Table.vue";
    import SearchInputs from "./SearchInputs.vue";
    //接口
    import {
        getListPageRoleUser,
        getUserJpoint,
        postBindJpoint,
        postUnbindJpoint,
        postRegister,
        getUserJprocess,
        postUnbindJprocess,
        postbindJprocess,
    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool.js";
    export default {
        extends: Index, //继承通用index
        name: 'userBindPointsIndex',
        components: {
            BoxTitle,
            SearchInputs,
            Table
        },
        data() {
            return {
                showAdd: false,
                addFormObj: {},
                addDialogShow: false, //新增用户弹窗
                points: [],

                dialogShowJpRecord: false,
                bindRecordTableData: [], //绑定的记录
                recordtotal: 0, //绑定的记录总数
                selectedRows: [], //选中的项
                selectedRowsSearchRecord: [], //搜索记录选中的项
                showAddContent: false, //新增绑定记录的界面
                searchRecordTableData: [], //搜索的结果 待绑定

                recordForm: {}, //添加记录的搜索条件对象
                recordOptions: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '1rem'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                addRule: [{
                    type: 'input',
                    field: 'cellphone',
                    title: '手机号：',
                    value: '',
                    validate: [{
                        required: true,
                        message: '请输入手机号',
                        trigger: 'blur'
                    }],
                    col: {
                        span: 24
                    }
                }, {
                    type: 'input',
                    field: 'realName',
                    title: '姓名：',
                    value: '',
                    validate: [{
                        required: true,
                        message: '请输入姓名',
                        trigger: 'blur'
                    }],
                    col: {
                        span: 24
                    }
                }],
                rule: [{
                    type: 'AutoCompleteQuery',
                    field: 'jpCode',
                    title: '计量点名称：',
                    value: '',
                    props: {
                        placeholder: '请选择计量点或输入名称搜索',
                        fieldValue: 'jpCode',
                        fieldLabel: 'jpName',
                        queryFunc: (keyWord) => {
                            const opt = {
                                _loading: false,
                                bind: false,
                                userId: this.currEditRow.id
                            }
                            if (keyWord !== '') {
                                opt.jpName = keyWord;
                            }
                            return getUserJpoint(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.jpName
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    col: {
                        span: 24
                    }
                }],
                addRecordRule: [{
                    type: 'input',
                    field: "jpName",
                    title: '计量点名称：',
                    value: '',
                    props: {
                        placeholder: '请输入计量点名称',
                    },
                    validate: [{
                        validator: (rule, value, callback) => {
                            if (value === '') {
                                callback(new Error('计量点名称不能为空'));
                            } else if (value.trim() == '') {
                                callback(new Error('请输入非空格字符'));
                            } else {
                                callback();
                            }
                        },
                        required: true,
                        trigger: 'blur'
                    }],
                    col: {
                        span: 16,
                    }
                }, {
                    type: 'SubmitBtns',
                    field: 'btn',
                    emit: ['search', 'reset'],
                    col: {
                        span: 6,

                    }
                }],

            }
        },
        methods: {
            //操作按钮点击事件
            btnClick(btnName, row) {
                this.currEditRow = row; // 记录当前更改的数据
                if (btnName == '计量点') {
                    this.dialogShow = true;
                    this._updatePoints();
                } else if (btnName == '用电记录') {
                    this._updateUsersJpRecoed();
                    this.dialogShowJpRecord = true;
                }
            },
            //解绑
            removeRecord() {
                postUnbindJprocess({
                    userJprocessList: this.selectedRows.map(item => ({
                        ...item
                    }))
                }).then(res => {
                    this.success();
                    this._searchRecordAjax();
                })
            },
            //新增绑定提交
            addRecord() {
                postbindJprocess({
                    userJprocessList: this.selectedRowsSearchRecord.map(item => ({
                        ...item,
                        userId: this.currEditRow.id
                    }))
                }).then(res => {
                    this.success();
                    this._searchRecordAjax();
                })
            },
            //统一提示
            success() {
                this.$message.success('操作成功');
                this._updateUsersJpRecoed();
            },
            //选择已绑定的记录
            handleSelectionChange(val) {
                this.selectedRows = val;
            },
            //选择搜索记录
            handleSelectionChangeSearchRecord(val) {
                this.selectedRowsSearchRecord = val;
            },
            //根据计量点名称查询用电记录
            recordSearch() {
                this.recordForm.validate((valid, fail) => {
                    if (valid) {
                        this._searchRecordAjax();
                    }
                })
            },
            //根据搜索条件查询
            _searchRecordAjax() {
                if (!this.recordForm.formData().jpName || this.recordForm.formData().jpName.trim() == '') {
                    return;
                }
                getUserJprocess({
                    userId: this.currEditRow.id,
                    bind: false,
                    jpName: this.recordForm.formData().jpName,
                }).then(res => {
                    this.searchRecordTableData = res.data ? res.data : [];
                    if (res.data.length == 1) {
                        //只有唯一一条结果 则默认选中
                        this.$nextTick(() => {
                            this.$refs.searchTable.toggleRowSelection(this
                                .searchRecordTableData[0], true);
                        })
                    }
                })
            },
            //重置搜索表单和记录
            recordReset() {
                this.recordForm.resetFields && this.recordForm.resetFields();
                this.searchRecordTableData = [];
            },
            //更新用户绑定的用电记录
            _updateUsersJpRecoed() {
                getUserJprocess({
                    userId: this.currEditRow.id,
                    bind: true,
                }).then(res => {
                    this.bindRecordTableData = res.data ? res.data : [];
                    this.recordtotal = this.bindRecordTableData.length;
                })

            },
            //更新计量点
            _updatePoints() {
                getUserJpoint({
                    userId: this.currEditRow.id,
                    bind: true,
                }).then(res => {
                    this.points = res.data;
                })
            },
            //默认获取表格数据和搜索数据
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                getListPageRoleUser({
                    page: this.page.pageNum,
                    rows: this.page.pageSize,
                    roleName: '井队人员', //井队角色id
                    ...this.search
                }).then(res => {
                    this.tableData = res.data;
                    this.total = res.total;
                })
            },
            cancelAddRecord() {
                this.showAddContent = false;
                this.recordReset();
            },
            //搜索按钮
            searchHand(data) {
                this.page.pageNum = 1; //搜索时页数置为1
                this.search = data;
                this._getData();
            }, //操作按钮点击的事件
            //表单提交
            submit() {
                this.formObj.validate((valid, fail) => {
                    if (valid) {
                        const data = this.formObj.formData();
                        //todo 表单验证通过
                        this.loading = true;
                        if (this.currEditRow !== 'new') { //编辑
                            postEditBasicLine({
                                ...this.currEditRow,
                                ...data,
                            }).then(this.notice).catch(err => {
                                this.loading = false; //加载
                                return false
                            })
                        } else { //新增
                            postAddBasicLine({
                                ...data,
                            }).then(this.notice).catch(err => {
                                this.loading = false; //加载
                                return false
                            })
                        }
                    }
                })
            },
            addClick() {
                this.showAdd = true;
            },
            cancel() {
                this.showAdd = false;
                this.formObj.exec('jpCode', 'clear');
            },
            //现在用户
            addHand() {
                this.currEditRow = 'new'; // 记录当前更改的数据
                this.addDialogShow = true;
            },
            addUser() {
                this.addFormObj.validate((valid, fail) => {
                    if (valid) {
                        const data = this.addFormObj.formData();
                        this.loading = true;
                        postRegister({
                            ...data,
                            userName: data.cellphone,
                        }).then(res => {
                            this.loading = false; //加载
                            this.addDialogShow = false;
                            this.$message.success('新增成功');
                            this.reloadTable();
                        }).catch(err => {
                            this.loading = false; //加载
                        })
                    }
                })
            },
            add() {
                const data = this.formObj.formData();
                postBindJpoint({
                    jpCode: data.jpCode,
                    alive: this.points.length == 0 ? 1 : 0,
                    userId: this.currEditRow.id,
                }).then(res => {
                    this._updatePoints();
                    this.showAdd = false;
                    this.formObj.exec('jpCode', 'clear');
                    this.$message.success('绑定成功');
                }).catch(err => {
                    this.$message.error('绑定失败' + JSON.stringify(err));
                })
            },
            deletePoint(item) {
                postUnbindJpoint(item).then(res => {
                    this._updatePoints();
                    this.$message.success('解绑成功');
                }).catch(err => {
                    this.$message.error('绑定失败' + JSON.stringify(err));
                })
            },
            //重写关闭弹窗
            handleCloseAdd() {
                this.addDialogShow = false;
            },
            //关闭弹出 初始化
            handleCloseJpRecord() {
                this.dialogShowJpRecord = false;
                this.recordReset();
                this.selectedRows = [];
                this.selectedRowsSearchRecord = [];
                this.searchRecordTableData = [];
                this.recordtotal = 0;
                this.bindRecordTableData = [];
                this.showAddContent = false;
            },
            //重写关闭弹窗
            handleClose() {
                this.dialogShow = false;
                this.showAdd = false;
                this.points = [];
            }
        },
        activated: function () {
            this._getData();
        },
        deactivated: function () {
            // 移除任何你不想保留的数据
            this.tableData = [];
        }
    }
</script>
<style scoped lang="scss">
    .add-content {
        .input-div {
            width: 100%;
        }

        .add-btns {
            margin-top: 20px;
        }
    }

    .noData {
        line-height: 48px;
        text-align: center;
        color: #606266;
    }

    .line {
        height: 20px;
        border-bottom: 1px solid #ddd;
    }

    .marginTop {
        margin-top: 16px;
    }

    /deep/ .el-tag {
        margin-right: 20px;
        margin-bottom: 16px;
    }

    /deep/ .el-form-item {
        margin-bottom: 0;
    }

    .item {
        margin-bottom: 16px;
    }

    .notice {
        margin-left: 20px;

    }
</style>