<!--变电站table-->
<template>
    <div style="width: 100%" class="cus-table">
        <el-table row-key="index" :data="tableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}"
            :row-class-name="tableRowClassName">
            <el-table-column label="序号" type="index" width="90" />
            <el-table-column label="头像">
                <template slot-scope="scope">
                    <div class="avatar">
                        <img :src="scope.row.avatar ?scope.row.avatar:defaultImg " />
                    </div>
                </template>
            </el-table-column>
            <!-- <el-table-column label="用户名" prop="userName"></el-table-column> -->
            <el-table-column label="手机号" prop="cellphone"></el-table-column>
            <el-table-column label="姓名" prop="realName"></el-table-column>
            <!-- <el-table-column label="微信昵称" prop="nickName"></el-table-column> -->


            <el-table-column label="操作" align="center" class-name="cusTable" width="130">
                <template slot-scope="scope">
                    <el-dropdown @command="handleCommand($event,scope.row)">
                        <span class="el-dropdown-link cus-btn">
                            绑定<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="计量点">绑定计量点</el-dropdown-item>
                            <el-dropdown-item command="用电记录">绑定用电记录</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>

                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="prev, pager, next, jumper,total, sizes"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import Table from "../components/Table.vue";
    import defaultImg from "@/assets/logo1.png";
    export default {
        extends: Table,
        data() {
            return {
                defaultImg
            }
        },
        methods: {
            handleCommand(item, row) {
                this.$emit('btnClick', item, row)
            }
        }
    }
</script>

<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding-left: 25px;
        cursor: pointer;
        line-height: 22px;
        position: relative;

        &:first-child {
            padding-left: 0;
        }
    }

    .table-btns {
        cursor: pointer;
        display: flex;
        padding: 0 10px;
        justify-content: space-between;
        align-items: center;

        .edit-btn:hover {
            color: #2E4AF7
        }

        .delete-btn:hover {
            color: #E6A23C
        }

        .center {
            width: 100%;
        }

        .btn {
            color: #2E4AF7;
            text-decoration: underline
        }

    }


    /deep/ .active-row {
        background-color: #F5F7FA;
    }

    .avatar {
        display: flex;
