<script>
    export default {
        data() {
            return {
                formObj: {},
                options: {
                    form: {
                        labelPosition: 'left',
                        labelWidth: '90px'
                    },
                    submitBtn: false,
                    resetBtn: false
                },
                //表单生成规则
                rule: []
            }
        },
        mounted() {
            // this.search();
        },
        methods: {
            //根据字段名称找到规则
            _findRule(type) {
                return this.rule.find(r => r.field === type);
            },
            down() {
                this.$emit('downHand');
            },
            search() {
                const formData = this.formObj.formData();
                const opt = {
                    ...formData,
                }
                for (let key in opt) {
                    if (opt[key] == '' || opt[key] == undefined) delete opt[key];
                }
                this.$emit('searchHand', opt)
            },
            //重置
            reset() {
                //重置表单
                this.formObj.resetFields();
                let rules = this.formObj.model();
                //重置自定义组件
                for (let key in rules) {
                    let item = rules[key];
                    if (item.type == "AutoCompleteQuery") {
                        this.formObj.exec(item.field, 'clear')
                    }
                }
                //触发父组件方法查询
                this.$emit('searchHand', {})
            }
        }
    }
</script>
<style lang="scss">
    .search {
        padding: 24px 0 0px 0;

        .el-form-item {
            margin-bottom: 0.16rem;
        }

        .li {
            display: flex;
            margin-bottom: 16px;
        }
    }
</style>